<template>
  <div id="show">
    <div class="center"></div>
    <div id="fourlView"></div>
  </div>
</template>
 
<script>
import * as THREE from 'three'
import { WEBGL } from 'three/examples/jsm/WebGL.js';
import Stats from 'three/examples/jsm/libs/stats.module';
import { GUI } from 'three/examples/jsm/libs/dat.gui.module.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';


export default {
  name: 'fourlView',
  data () {
    return {
      container: null, stats: null,
      scene: null, renderer: null,
      mouseX: 0, mouseY: 0,
      windowWidth: null, windowHeight: null,
      views: [
        {
          left: 0,  //左下
          bottom: 0,
          width: 0.5,
          height: 0.5,
          background: new THREE.Color(0x000000),
          eye: [0, 3, 10], //前
          up: [-1, 1, 0],
          fov: 45,

        },
        {
          left: 0,  //左上
          bottom: 0.5,
          width: 0.5,
          height: 0.5,
          background: new THREE.Color(0x000000),
          eye: [-10, 3, 0],//左
          up: [0, -1, -1],
          fov: 45,

        },
        {
          left: 0.5,//右下
          bottom: 0,
          width: 0.5,
          height: 0.5,
          background: new THREE.Color(0x000000),
          eye: [10, 3, 0],//右
          up: [-1, 1, -1],
          fov: 45,

        },
        {
          left: 0.5,//右上
          bottom: 0.5,
          width: 0.5,
          height: 0.5,
          background: new THREE.Color(0x000000),
          eye: [0, 3, -10],//后
          up: [-1, -1, 0],

          fov: 45,

        }
      ],
      clock: null, gui: null, mixer: null, actions: null, activeAction: null, previousAction: null,
      mesh: null,
      model: null,
      face: null,
      api: { state: 'Walking' }

    }
  },
  methods: {
    init: function () {

      this.container = document.getElementById('fourlView');

      for (var ii = 0; ii < this.views.length; ++ii) {

        var view = this.views[ii];
        var camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.25, 100);

        // var camera = new THREE.PerspectiveCamera(view.fov, window.innerWidth / window.innerHeight, 1, 10000);

        camera.position.fromArray(view.eye);
        camera.up.fromArray(view.up);
        view.camera = camera;

      }
      // robot模型开始
      this.scene = new THREE.Scene();
      // this.scene.background = new THREE.Color(0xe0e0e0);
      // this.scene.fog = new THREE.Fog(0xe0e0e0, 20, 100);

      this.clock = new THREE.Clock();

      // // lights

      // var light = new THREE.DirectionalLight(0xffffff);
      // light.position.set(0, 0, 1);
      // this.scene.add(light);

       var light = new THREE.HemisphereLight(0xffffff, 0x444444);
      light.position.set(0, 20, 0);
      this.scene.add(light);

      light = new THREE.DirectionalLight(0xffffff);
      light.position.set(0, 20, 10);
      this.scene.add(light);


      // // ground

      // var mesh = new THREE.Mesh(new THREE.PlaneBufferGeometry(2000, 2000), new THREE.MeshPhongMaterial({ color: 0x999999, depthWrite: false }));
      // mesh.rotation.x = - Math.PI / 2;
      // this.scene.add(mesh);

      // var grid = new THREE.GridHelper(200, 40, 0x000000, 0x000000);
      // grid.material.opacity = 0.2;
      // grid.material.transparent = true;
      // this.scene.add(grid);

      // model

      var loader = new GLTFLoader();
      var self = this;
      loader.load('./static/three/RobotExpressive/RobotExpressive.glb', function (gltf) {
        self.model = gltf.scene;
        self.scene.add(self.model);

        self.createGUI(self.model, gltf.animations);

      }, undefined, function (e) {

        window.console.error(e);

      });

      // robot模型结束


      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      // 加了之后颜色浅一点
      this.renderer.outputEncoding = THREE.sRGBEncoding;

      this.container.appendChild(this.renderer.domElement);


      this.stats = new Stats();
      this.container.appendChild(this.stats.dom);


    },
    createGUI: function (model, animations) {
      var states = ['Idle', 'Walking', 'Running', 'Dance', 'Death', 'Sitting', 'Standing'];
      var emotes = ['Jump', 'Yes', 'No', 'Wave', 'Punch', 'ThumbsUp'];

      this.gui = new GUI();

      this.mixer = new THREE.AnimationMixer(model);

      this.actions = {};

      for (var i = 0; i < animations.length; i++) {

        var clip = animations[i];
        var action = this.mixer.clipAction(clip);
        this.actions[clip.name] = action;

        if (emotes.indexOf(clip.name) >= 0 || states.indexOf(clip.name) >= 4) {

          action.clampWhenFinished = true;
          action.loop = THREE.LoopOnce;

        }

      }

      // states

      var statesFolder = this.gui.addFolder('States');

      var clipCtrl = statesFolder.add(this.api, 'state').options(states);

      clipCtrl.onChange(function () {

        self.fadeToAction(self.api.state, 0.5);

      });

      statesFolder.open();

      // emotes

      var emoteFolder = this.gui.addFolder('Emotes');
      var self = this;
      function createEmoteCallback (name) {

        self.api[name] = function () {

          self.fadeToAction(name, 0.2);

          self.mixer.addEventListener('finished', restoreState);

        };

        emoteFolder.add(self.api, name);

      }

      function restoreState () {

        self.mixer.removeEventListener('finished', restoreState);

        self.fadeToAction(self.api.state, 0.2);

      }

      for (var j = 0; j < emotes.length; j++) {

        createEmoteCallback(emotes[j]);

      }

      emoteFolder.open();

      // expressions

      this.face = this.model.getObjectByName('Head_2');

      var expressions = Object.keys(this.face.morphTargetDictionary);
      var expressionFolder = this.gui.addFolder('Expressions');

      for (var k = 0; i < expressions.length; k++) {

        expressionFolder.add(this.face.morphTargetInfluences, k, 0, 1, 0.01).name(expressions[k]);

      }

      this.activeAction = this.actions['Walking'];
      this.activeAction.play();

      expressionFolder.open();

    },
    fadeToAction: function (name, duration) {

      this.previousAction = this.activeAction;
      this.activeAction = this.actions[name];

      if (this.previousAction !== this.activeAction) {

        this.previousAction.fadeOut(duration);

      }

      this.activeAction
        .reset()
        .setEffectiveTimeScale(1)
        .setEffectiveWeight(1)
        .fadeIn(duration)
        .play();

    },
    updateSize: function () {

      if (this.windowWidth != window.innerWidth || this.windowHeight != window.innerHeight) {

        this.windowWidth = window.innerWidth<window.innerHeight? window.innerWidth:window.innerHeight;
        this.windowHeight = this.windowWidth;
        var bigbox = document.getElementById('show');
        bigbox.style.width=this.windowWidth;
        bigbox.style.height=this.windowWidth;

        this.renderer.setSize(this.windowWidth, this.windowHeight);

      }
    },
    animate: function () {

      var dt = this.clock.getDelta();

      if (this.mixer) this.mixer.update(dt);

      this.render();
      this.stats.update();

      requestAnimationFrame(this.animate);






    },
    render: function () {

      this.updateSize();

      for (var ii = 0; ii < this.views.length; ++ii) {

        var view = this.views[ii];
        var camera = view.camera;

        // view.updateCamera(camera, this.scene, this.mouseX, this.mouseY);
        camera.lookAt(new THREE.Vector3(0, 2, 0));

        var left = Math.floor(this.windowWidth * view.left);
        var bottom = Math.floor(this.windowHeight * view.bottom);
        var width = Math.floor(this.windowWidth * view.width);
        var height = Math.floor(this.windowHeight * view.height);

        this.renderer.setViewport(left, bottom, width, height);
        this.renderer.setScissor(left, bottom, width, height);
        this.renderer.setScissorTest(true);
        this.renderer.setClearColor(view.background);

        camera.aspect = width / height;
        camera.updateProjectionMatrix();

        this.renderer.render(this.scene, camera);

      }

    },
    // // 添加图片
    // addImageBitmap:function () {

		// 		new THREE.ImageBitmapLoader()
		// 			.setOptions( { imageOrientation: 'none' } )
		// 			.load( '~assets/logo.png', function ( imageBitmap ) {

		// 				var texture = new THREE.CanvasTexture( imageBitmap );
		// 				var material = new THREE.MeshBasicMaterial( { map: texture } );

		// 				/* ImageBitmap should be disposed when done with it
		// 				   Can't be done until it's actually uploaded to WebGLTexture */

		// 				// imageBitmap.close();

		// 				addCube( material );

		// 			}, function ( p ) {

		// 				console.log( p );

		// 			}, function ( e ) {

		// 				console.log( e );

		// 			} );

		// 	},addCube:function ( material ) {

		// 		var cube = new THREE.Mesh( geometry, material );
		// 		cube.position.set( Math.random() * 2 - 1, Math.random() * 2 - 1, Math.random() * 2 - 1 );
		// 		cube.rotation.set( Math.random() * 2 * Math.PI, Math.random() * 2 * Math.PI, Math.random() * 2 * Math.PI );
		// 		cubes.add( cube );

		// 	}

  },
  mounted () {
    // 检验是否兼容webGL
    if (WEBGL.isWebGLAvailable()) {
      // Initiate function or other initializations here
      this.init();
      this.animate()
    } else {
      var warning = WEBGL.getWebGLErrorMessage();
      document.getElementById('container').appendChild(warning);
    }

  }
}
</script>

<style lang="less" scoped>
#show{
  background:black;
  overflow:hidden;
  .center{
    width: 5px;
    height: 5px;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);    
  }
  #fourlView{
  transform: rotate(-45deg);
}
    
}

</style>
